import Vue from 'vue'
import Router from 'vue-router'
////
////////////////+
import Index from "@/components/index";
///////
//1.
import bbjy from "@/components/bbjy/bbjy"
///1.1
import bzzl from "@/components/public/bzzl"
///1.2
import ckgd from "@/components/public/ckgd"
//2.
import Taocoin from "@/components/Taocoin/Taocoin"
//2.1
import Exchange from "@/components/Taocoin/children/Exchange"
//2.2
import Exchangeofdetails from "@/components/Taocoin/children/Exchangeofdetails"
//2.3
import SuccessfulRedemption from "@/components/Taocoin/children/SuccessfulRedemption";
//3.
import fbjy from "@/components/fbjy/fbjy"
//34.
import ddxq from "@/components/fbjy/ddxq"
//4.
import sbzx from "@/components/sbzx/sbzx"
//5.
import bzzx from "@/components/bzzx/bzzx"
//6.
import shouye from "@/components/shouye/shouye"
//6.1
import transactionTransaction from "@/components/shouye/children/introduce"
//7.
import login from "@/components/login/login"
///7.1
import forget from "@/components/public/forget"
//8.
import register from "@/components/register/register"
//9.
import userCenter from "@/components/userCenter/userCenter"
////9.1
import Account_security	from "@/components/userCenter/children/Account_security";
////9.2
import identity from "@/components/userCenter/children/identity"
////9.3
import zhanghu from "@/components/userCenter/children/zhanghu"
////9.4
import myCard from "@/components/userCenter/children/myCard"
////10.
import capital from "@/components/capital/capital"
////10.1 
import Trading_account from "@/components/capital/children/Trading_account"
////10.2 
import Red_Coins from "@/components/capital/children/Red_Coins"
////10.3 
import tiBi from "@/components/capital/children/tibi"
////10.4 
import money_Record from "@/components/capital/children/money_Record"
////10.5
import Currency_address from "@/components/capital/children/Currency_address"
////11.API
import api_admin from "@/components/api_admin/api_admin"
////12.
import rate from "@/components/rate/rate"
////12.1
import transaction from "@/components/rate/children/transaction"
////12.2VIP
import transactionvip from "@/components/rate/children/transactionvip"
////13.
import notice from "@/components/notice/notice"
////14.
import details from "@/components/notice/details"
////15.
import My_Invitation_Code from "@/components/InvitationCode/InvitationCode"
////15.1
import InvitationList from "@/components/InvitationCode/children/List"
////16.
import Agreement from "@/components/public/Agreement"
////16.1
import Privacy from "@/components/public/Privacy"
////17.
import Help from "@/components/public/Help"
////18.
import TradingRules from "@/components/public/jygz"
////19.
import RevenueRecord from "@/components/capital/children/RevenueRecord"
////20.
import AccumulationOfAssets from "@/components/shouye/children/AccumulationOfAssets"
////21.()
import inviteFriends from "@/components/shouye/children/inviteFriends"
////22.
import Repurchasedetails from "@/components/shouye/children/Repurchasedetails"
////23.
import  TaocoinDetails   from "@/components/Taocoin/children/TaocoinDetails"
////23.
import  Applicationcurrency from "@/components/ApplicationCurrency/Applicationcurrency"
///24.
import Airdrop from "@/components/airdrop/airdrop"
///25.
import AirdropDetails from "@/components/airdrop/children/airdropDetails"
///26.
import vote from "@/components/vote/vote"
///26.1
import voteDetails from "@/components/vote/children/voteDetails"
///27.
import wallet from "@/components/wallet/wallet"
///
import TaocoinExchangeRules from "@/components/Taocoin/children/TaocoinExchangeRules"
///
import supperNode from "@/components/supperNode/supperNode"
///
import supperNodeDetails from "@/components/supperNode/children/supperNodeDetails"
///IMX
import currencyRules from "@/currencySystem/children/currencyRules"
///
import voteCoinProcess from "@/currencySystem/children/voteCoinProcess"
///
import currencySystem from "@/currencySystem/currencySystem"
// 
import Applicationcurrencymore from "@/components/ApplicationCurrencyMore/Applicationcurrencymore"
// 
import activity from "@/components/activity/activity"
//
import voteCoin from "@/components/voteCoin/voteCoin"
//
import Authentication from "@/components/userCenter/children/Authentication"
Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect:"/index"
    },
    {path:"/Agreement",component:Agreement},	 //
    {path:"/Privacy",component:Privacy},       //
    {path:"/Help",component:Help},						 //
    {
    	path:"/index",
    	component:Index,
    	children:[
      	{path:"/index",redirect:"/index/Home"},				 //
    		{path:"/index/Home",component:shouye},				 //
        {path:"/index/transactionTransaction",name:"toTransactionTransaction",component:transactionTransaction},     //
    		{path:"/index/inviteFriends",name:"inviteFriends",component:inviteFriends},  //()
    		{path:"/index/Repurchasedetails",name:"Repurchasedetails",component:Repurchasedetails},  //
    		{path:"/index/notice",component:notice},				 //
    		{path:"/index/details/:id",name:"details",component:details},				 //
    		{path:"/index/trade/macthid:id",name:"bbjy",component:bbjy},  					 //
    		{path:"/index/bzzl/:id",name:"bzzl",component:bzzl}, //
    		{path:"/index/More/macthid:id",name:"More",component:ckgd},          	 //
    		{path:"/index/Taocoin",name:"Taocoin",component:Taocoin},	 								 //
    		{path:"/index/Exchange",name:"exchange",component:Exchange},    //
    		{path:"/inded/SuccessfulRedemption",name:"successfulRedemption",component:SuccessfulRedemption},  //
    		{path:"/index/Exchangeofdetails/:id",name:"Exchangeofdetails",component:Exchangeofdetails},  //
    		{path:"/index/TaocoinDetails/:id",name:"TaocoinDetails",component:TaocoinDetails},     //
    		{path:"/index/otc",name:"otc",component:fbjy},	 					 //
    		{path:"/index/ddxq/:id",name:"ddxq",component:ddxq},						 //
    		{path:"/index/sbzx",component:sbzx},	 					 //
    		{path:"/index/Help",component:bzzx},	 					 //
    		{path:"/index/login",name:"login",component:login}, 				   //
    		{path:"/index/forget",name:"forget",component:forget},         //
    		{path:"/index/register",name:"register",component:register}, 	   //
    		{path:"/index/api_admin",component:api_admin},   //API
    		{path:"/index/InvitationCode",name:"InvitationCode",component:My_Invitation_Code},  //
    		{path:"/index/InvitationList",component:InvitationList},  //
    		{path:"/TradingRules",component:TradingRules},	 //
        {path:"/TaocoinExchangeRules",name:"TaocoinExchangeRules",component:TaocoinExchangeRules},
        {path:"/index/RevenueRecord",component:RevenueRecord}, //
    		{path:"/index/AccumulationOfAssets/:type",name:"AccumulationOfAssets",component:AccumulationOfAssets}, //
    		{path:"/index/Applicationcurrency",component:Applicationcurrency},         //
    		{name:'moneyUp',path:"/index/Applicationcurrencymore", components: { default: Applicationcurrencymore},props: { default: true}},           // 0  ,1 ,2
    		{path:"/index/Airdrop",name:"Airdrop",component:Airdrop},   //
    		{path:"/index/AirdropDetails/:id",name:"AirdropDetails",component:AirdropDetails}, //
    		{path:"/index/vote",name:"vote",component:vote},  //
    		{path:"/index/voteDetails/:id",name:"voteDetails",component:voteDetails}, //
    		{path:"/index/wallet",name:"wallet",component:wallet},  //
        {path:"/index/supperNode",name:"supperNode",component:supperNode}, //
        {path:"/index/supperNodeDetails",name:"supperNodeDetails",component:supperNodeDetails}, //
        {path:"/index/currencyRules",name:"currencyRules",component:currencyRules}, //IMX
        {path:"/index/voteCoinProcess",name:"voteCoinProcess",component:voteCoinProcess}, //
        {path:"/index/currencySystem",name:"currencySystem",component:currencySystem}, //
        {path:"/index/voteCoin",name:"voteCoin",component:voteCoin}, // 
        {path:"/index/activity",component:activity}, //fo  eos 
    		{
    			path:"/index/rate",																//
    			component:rate,
    		  	children:[
    				{path:"/index/rate",redirect:"/index/rate/transaction"},
    				{path:"/index/rate/transaction",component:transaction},  /////
    				{path:"/index/rate/transactionvip",component:transactionvip},									////
    			]
    		},
    		{
    			path:"/index/userCenter",																									//
    			component:userCenter,
    			children:[
    				{path:"/index/userCenter",redirect:"/index/userCenter/Accountsecurity/no"},
    				{path:"/index/userCenter/Accountsecurity/:bangdin",name:"Accountsecurity",component:Account_security},  /////
    				{path:"/index/userCenter/identity/:type",name:"identity",component:identity},	////
            {path:"/index/userCenter/Authentication",name:"Authentication",component:Authentication}, //+
    				{path:"/index/userCenter/zhanghu",name:"zhanghu",component:zhanghu},										////
    				{path:"/index/userCenter/myCard",component:myCard}											///
    			]
    		},
    		{
    			path:"/index/capital",																										//
    			component:capital,
    			children:[
    				{path:"/index/capital",redirect:"/index/capital/Trading_account"},
    				{path:"/index/capital/Trading_account",name:"Trading_account",component:Trading_account}, 			 /////
    				{path:"/index/capital/Red_Coins",name:"Red_Coins",component:Red_Coins},		////
    				{path:"/index/capital/tibi",name:"tibi",component:tiBi},									////
    				{path:"/index/capital/money_Record",component:money_Record},							 ///
    				{path:"/index/capital/Currency_address",component:Currency_address}				 ///
    			]
    		}
    	]
    }
  ]
})



