<template>
  <div id="currencyRules">
    <!--IMX-->
    <div class="currencyContainer" :class="{'white-text':getColor==1}">
      <div class="currencyTitle" :class="{'white-active-bg':getColor==1}">
        <p class="text">IMX</p>
      </div>
      <!--IMX-->
      <div class="currency_information" :class="{'font_color':getColor==1}">
        <!---->
        <div class="content">
          <ul class="instructions">
            <li class="content_title">
              <p class="bigTitle"></p>
              <p class="smallTitle">IMXCoin/Token</p>
              <div class="innerContent">
                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
                <p> </p>
              </div>
            </li>
            <li class="content_title">
              <p class="bigTitle"></p>
              <p class="smallTitle">IMX</p>
              <div class="innerContent">
                <p> </p>
                <p> </p>
                <p> </p>
                <p> 3010</p>
                <p> 5</p>
                <p> </p>
                <p> </p>
                <p> </p>
              </div>
            </li>
            <li class="content_title">
              <p class="bigTitle">IMX530</p>
            </li>
          </ul>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        // getColor: window.localStorage.getItem("setColor"),
      }
    }
  }
</script>

<style lang="scss">

  .font_color{
    color: #000 !important;
    background-color: #fff !important;
    border: 1px solid #ddd !important;
  }
  .white-active-bg {
    color: #fff !important;
    background: #312e67 !important;
  }
  .white-text {
    .currency-title {
      border-bottom: 1px solid #ddd !important;
    }
    .currency-information {
      color: #000 !important;
      border: 1px solid #ddd !important;
    }
  }
  #currencyRules{
    width: 100%;
    min-height:100%;
    padding-bottom:100px;
  }
  .currencyContainer {
    width:1200px;
    margin:0 auto;
    font-size: 14px;
    color:#9b9fbf;
    .currencyTitle {
      width: 100%;
      color:#c8cce4;
      background: #17172c;
      height:60px;
      line-height: 60px;
      border-radius: 4px;
      font-size:20px;
      margin-top:56px;
      margin-bottom:36px;
      padding-left:24px;
      box-sizing: border-box;
    }
    /**/
    .currency_information {
      width: 100%;
      font-size: 15px;
      color: #fff;
      background: #16182c;
      .content{
        box-sizing: border-box;
        padding-left:34px;
        padding-bottom:30px;
      }
    }
  }
.textIndex{
  margin-left:28px;
  box-sizing: border-box;
  line-height: 20px;
}
.instructions{
  box-sizing: border-box;
  padding-top:35px;
  .content_title{
    margin-bottom:35px;
    .bigTitle{
      font-size:17px;
      padding-bottom:26px;
      color:#afb2c8;
    }
    .smallTitle{
      font-size:15px;
      padding-bottom:8px;
      color:#afb2c8;
    }
  }
  .innerContent{
    font-size:14px;
    line-height:32px;
    color:#8184a1;
  }
}
</style>



