						if(_this.OptionalList.main.length == 0 && _this.OptionalList.innovation.length == 0) {
							_this.isEmpoet = true;
						} else {
							_this.isEmpoet = false;
						}
					}
				}
			},
			////()
			notZhu(item, $event, index) {
				var _this = this;
				_this.OptionalList.main.splice(index, 1); ///
				for(let i = 0; i < _this.itemslocalstorge.length; i++) { ///id
					if(_this.itemslocalstorge[i] == item.matchcoin_id) {
						_this.itemslocalstorge.splice(i, 1);
						i--;
					}
				}
				//
				if(_this.OptionalList.main.length == 0 && _this.OptionalList.innovation.length == 0) {
					_this.isEmpoet = true;
				} else {
					_this.isEmpoet = false;
				}
			},
			////()
			notNew(item, $event, index) {
				var _this = this;
				_this.OptionalList.innovation.splice(index, 1); ///
				for(let i = 0; i < _this.itemslocalstorge.length; i++) { ///id
					if(_this.itemslocalstorge[i] == item.matchcoin_id) {
						_this.itemslocalstorge.splice(i, 1);
						i--;
					}
				}
				//
				if(_this.OptionalList.main.length == 0 && _this.OptionalList.innovation.length == 0) {
					_this.isEmpoet = true;
				} else {
					_this.isEmpoet = false;
				}
			},
			////()
			removeZhu(item, $event, index) {
				var _this = this;
				///token
				_this.callback();
				////
				_this.OptionalList.main.splice(index, 1); ///
				axios.post(httpUrl.follow_matchcoin, {
					matchcoin_id: item.matchcoin_id
				}, {
					headers: {
						"Authorization": "Bearer " + _this.token
					}
				}).then(function(res) {
					if(res.data.data == false) {
						for(let i = 0; i < _this.getIcon.length; i++) { ///id
							if(_this.getIcon[i] == item.matchcoin_id) {
								_this.getIcon.splice(i, 1);
								i--;
							}
						}
					}
					//
					if(_this.OptionalList.main.length == 0 && _this.OptionalList.innovation.length == 0) {
						_this.isEmpoet = true;
					} else {
						_this.isEmpoet = false;
					}
				}, function(error) {
					console.log(error)
				})
			},
			///()
			removeNew(item, $event, index) {
				var _this = this;
				///token
				_this.callback();
				////
				_this.OptionalList.innovation.splice(index, 1); ///
				axios.post(httpUrl.follow_matchcoin, {
					matchcoin_id: item.matchcoin_id
				}, {
					headers: {
						"Authorization": "Bearer " + _this.token
					}
				}).then(function(res) {
					if(res.data.data == false) {
						for(let i = 0; i < _this.getIcon.length; i++) { ///id
							if(_this.getIcon[i] == item.matchcoin_id) {
								_this.getIcon.splice(i, 1);
								i--;
							}
						}
					}
					//
					if(_this.OptionalList.main.length == 0 && _this.OptionalList.innovation.length == 0) {
						_this.isEmpoet = true;
					} else {
						_this.isEmpoet = false;
					}
				}, function(error) {
					console.log(error)
				})

			}

		}

	}
</script>

