function getUrlParam(param) {
  //
  var reg = new RegExp('[&?]' + param + '=([^\\&]*)', 'i')
  //url?
  var hrefStr = window.location.search
  // URI 
  hrefStr = decodeURIComponent(decodeURIComponent(hrefStr))
  //
  var value = reg.exec(hrefStr)
  return value ? value[1] : ''
}



