</template>

<script>
	////
	import { httpUrl } from "../http_url/http_url";
	import { Message } from 'element-ui'; ///
	import Vue from 'vue'
	//  axios
	import Axios from 'axios'
	//jquery
	import $ from 'jquery'
	//loding
	import loading from "../public/loading"
	import {mapState} from 'vuex';
	export default {
		data() {
			return {
				isBinding: false,
				/**/
				sellCountDown: 40, ///
				sellTime: "", ///
				buyCountDown: 40, ///
				buyTime: "", ///
				mmbz: "", //
				bzDis: false, //()
				bzDis2: false, //()
				isMm: true, //
				isGdmm: false, //
				isShowUser: false, //
				isShowUser2: false, //
				bz: [], //
				bzID: 1, //ID
				bzType: "1", //
				mmddList: [], //
				wdddList: [], //
				userList: [], //
				ggxqmm: [], //
				ggxqbz: "", //
				authgrade: "", //authgrade
				mobile: "", //mobile
				ltbz: "", //type
				yeSlws: "", //
				xdgmSl: "", //
				xdgmJe: "", //
				maxgmsl: "", //
				maxgmje: "", //
				maxmcsl: "", //
				maxmcje: "", //
				xdmcSl: "", //
				xdmcJe: "", //
				mczjmm: "", //
				ggId: "", //Id
				mrslInfo: "", //
				mrjeInfo: "", //
				qdmrInfo: "", //
				mcslInfo: "", //
				mcjeInfo: "", //
				qdmcInfo: "", //
				mcmmInfo: "", //
				isMr: false, //
				isMc: false, //
				istxTk: false, //
				ismrjeInfo: true, //
				ismcmmInfo: true, //
				mmddType: 1, //
				tradednum: "", //
				mcbzye: "", //
				pagesize: 10, //
				mmfyList: [], //
				ddts: 0, //
				ddmysl: 0, //
				ddStatus: 0, //
				kjyfbs: [], //
				kjyfb: [], //
				kjyfbgj: "", //
				kjyfbgjId: "", //id
				gdbz: "", //
				gdList: [], //
				gdbzslws: 0, //
				gdts: 0, //
				gdmysl: 0, //
				gdjg: "", //
				fdbl: "", //
				tjfdbl: 0, //
				jyjg: 0, //
				rmbcode: "", //type
				rmbid: "", //id
				isCs: true, //
				isGd: true, //
				isZfts: true, //
				gdZffs: [], //
				scj: 0, //
				gdbzId: "", //id
				fkqx: 10, //
				jycs: 0, //
				minxe: 100, //
				maxxe: 150, //
				lynr: "", //
				tjlynr: "", //
				gjcheck: 0, //
				sjcheck: 0, //
				tycheck: 1, //
				jysl: "", //
				mmzt: 0, //
				sfgdjg: 1, //
				gdzjmm: "", //
				kjybzye: 0, //
				gdInfo: "", //
				slInfo: "", //
				zxInfo: "", //
				zdInfo: "", //
				qxInfo: "", //
				csInfo: "", //
				gdmmInfo: "", //
				fbInfo: "", //
				isfbInfo: false, //
				gdlbput: [], //
				gdbjId: "", //id
				isDisabled: false, //,
				isBj: true, //
				ismmanjy: false, //
				isgdlb: true, //
				jxddList: [], //
				jxmysl: 0, //
				jxts: 0, //
				listTitle: [this.$t('otc.Price'), this.$t('otc.MinAmount'), this.$t('otc.MaxAmount'), this.$t('otc.Principal'), this.$t('otc.PaymentMethod'), this.$t('otc.Operating')], //
				ddxqTitle: [this.$t('otc.OrderNumber'), this.$t('otc.CreationTime'), this.$t('otc.Currency'), this.$t('otc.Types'), this.$t('otc.TradingPrice'), this.$t('otc.Quantity'), this.$t('otc.Amount'), this.$t('otc.Fees'), this.$t('otc.Status'), this.$t('otc.ReferenceNo')], //
				gdmmTitle: [this.$t('fbjy.PendingOrderNum'), this.$t('otc.CreationTime'), this.$t('otc.Currency'), this.$t('otc.Types'), this.$t('otc.TradingPrice'), this.$t('list.Number'), this.$t('fbjy.OrderLimit'), this.$t('list.Action')], //
				jxddTitle: [this.$t('otc.OrderNumber'), this.$t('otc.CreationTime'), this.$t('otc.Currency'), this.$t('otc.Types'), this.$t('otc.TradingPrice'), this.$t('list.Number'), this.$t('otc.Status')], //
				gdmmlx: [{
					id: 1,
					title: this.$t("fbjy.Sell")
				}, {
					id: 0,
					title: this.$t("list.Buy_BTC")
				}], //
				gdsfgd: [{
					id: 1,
					title: this.$t("otc.Fixedprice")
				}, {
					id: 0,
					title: this.$t("fbjy.NonFixedPrice")
				}], //
				currencyCount: 1, //
				slPlace: "0.00", //,
				gdjjxzmin: 0, //
				gdjjxxmax: 0, //
				tabBuySell: false, //loading
				selectlegaltender: "", //
				selectItem: "", //
        orderId:0,  /*id*/
			}
		},
		components: { /*loading*/
			"loading": loading
		},
		computed:{
			...mapState('user',{yhId:state=>state.userID}),
		},
		mounted() {
			let _this = this;
			/******/
			//
			Axios.interceptors.request.use(config => {
				_this.tabBuySell = true;
				return config
			}, error => {
				_this.tabBuySell = false;
				_this.$message.error({
					message: _this.$t("userCenter.ResponseTimeAgain"),
					type: "warning"
				});
				return Promise.reject(error)
			})
			// 
			Axios.interceptors.response.use((response) => {
				if(response.data.status != undefined) {
					if(Number(response.data.status.code) !== 200) {
						_this.tabBuySell = false;
					}
				}
				if(response.status == 200) {
					_this.tabBuySell = false;
				}
				return response;
			}, (error) => {
				if(error.response) {
					_this.tabBuySell = false;
				}
				return Promise.reject(error)
			})

			/******/

			/////////////////////////////

			Axios.get(httpUrl.getbzUrl).then(function(res) {

				_this.bz = res.data.data
				//              id

				_this.gdbzId = _this.bz[0].id
				//
				_this.gdbzslws = _this.bz[0].displayNum
				//				
				_this.mmbz = _this.bz[0].pageIdentifier
				//				
				_this.gdbz = _this.bz[0].pageIdentifier
				//      BTC
				_this.gdjjxzmin = _this.bz[0].otc_min;
				//      BTC
				_this.gdjjxzmax = _this.bz[0].otc_max;
				//	                              
				///////////////////////////
				Axios.get(httpUrl.getmmddUrl, {
					params: {
						"currency_id": _this.gdbzId,
						"type": 1
					}
				}).then(function(res) {
					_this.mmddList = res.data.data;
					for(var i = 0, le = _this.mmddList.length; i < le; i++) {
						_this.uId = _this.mmddList[i].user_id
					}
					//				
					_this.mmfyList = _this.mmddList.slice(0, _this.pagesize)
				}).catch(function(err) {
					console.log("err", err)
				})
				if(_this.token == undefined || _this.token == null) { ///
					return false;
				} else {

					//			 
					Axios.get(httpUrl.getisbzyeUrl, {
						headers: {
							'Authorization': 'Bearer ' + _this.token
						},
						params: {
							"currency_id": _this.gdbzId
						}
					}).then(function(res) {
						//					
						_this.kjybzye = res.data.data.balance
						//					   
						var toFixedNum = Number(_this.kjybzye).toFixed(_this.gdbzslws + 1);
						_this.kjybzye = toFixedNum.substring(0, toFixedNum.toString().length - 1);
						//				        ,
						if(_this.mmzt == 0 || _this.kjybzye == 0) {
							_this.slPlace = "0.00" + _this.gdbz;
							return false;
						} else {
							_this.slPlace = _this.$t("fbjy.Available") + _this.kjybzye + _this.gdbz;
						}
					}).catch(function(err) {
						console.log("get err", err)
					})

				}
			}).catch(function(err) {
				console.log("err", err)
			})

			if(this.token == undefined || this.token == null) { ///
				return false;
			}
			//////////////////////////
			if(this.token == null || this.token == undefined) {
				return false;
			} else {
				Axios.get(httpUrl.getwdddUrl, {
					headers: {
						'Authorization': 'Bearer ' + _this.token
					},
					params: {
						stauts: 0
					}
				}).then(function(res) {
					//				
					_this.wdddList = res.data.data.data;
					//				
					_this.ddts = res.data.data.total
					//				
					_this.ddmysl = res.data.data.per_page
				}).catch(function(err) {
					console.log("err", err)
				})
				//////////////////////
				Axios.get(httpUrl.getkjyfbUrl, {
					headers: {
						'Authorization': 'Bearer ' + _this.token
					}
				}).then(function(res) {
					//				
					_this.kjyfbs = res.data.data
					//				
